/* Divider variables */
$divider-text-size-default: 14;
$divider-text-spacing-default: 30;
$divider-text-spacing-bottom: $divider-text-spacing-default - ( $divider-text-size-default + 6 ) + px;
$divider-text-color-default: $darkGray;
$divider-text-padding-default: 0 15px;
$divider-levels: (
    section: (
        margin: 40px 0,
        border-top-width: 2px,
        border-bottom: none
    ),
    subsection: (
        margin: 30px 0,
        border-top-width: 1px,
        border-bottom: none
    )
);
$divider-types: (
    shadow: (
        height: 4px,
        border: none,
        background-size: 98%  4px,
    ),
    solid: (
        height: auto,
        background: none,
        border-top-color: $lightGray,
        border-top-style: solid
    ),
    dashed: (
        height: auto,
        background: none,
        border-top-color: $lightGray,
        border-top-style: dashed
    )
);
$divider-spacing: (
    small: 10px 0,
    medium: 20px 0,
    large: 40px 0,
    text: 30px 0 $divider-text-spacing-bottom 0,
    none: 0
);

/* Divider mixins */
@mixin hr-divider($level){
    @if map-has-key($divider-levels, $level) {
        margin: map-get(map-get($divider-levels, $level), margin);
        border-top-width: map-get(map-get($divider-levels, $level), border-top-width);
        border-bottom: map-get(map-get($divider-levels, $level), border-bottom);
    }
    @else {
        @warn "Unknown `#{$level}` in $divider-levels.";
    }
}

@mixin hr-divider-type($type){
    @if map-has-key($divider-types, $type) {
        height: map-get(map-get($divider-types, $type), height);
        @if $type == 'shadow' {
            border: map-get(map-get($divider-types, $type), border);
            background-size: map-get(map-get($divider-types, $type), background-size);
            @include shadow-image(50% , top);
        }
        @else {
            background: map-get(map-get($divider-types, $type), background);
            border-top-color: map-get(map-get($divider-types, $type), border-top-color);
            border-top-style: map-get(map-get($divider-types, $type), border-top-style);
        }
    }
    @else{
        @warn "Unknown `#{$type}` in $divider-types.";
    }
}

@mixin hr-divider-spacing($size){
    @if map-has-key($divider-spacing, $size) {
        margin: map-get($divider-spacing, $size);
    }
    @else{
        @warn "Unknown `#{$size}` in $divider-sizes.";
    }
}

@mixin hr-divider--with-text{
    -ms-high-contrast-adjust: none;
    background-color: inherit;
    position: relative;
    display: flex;
    @include hr-divider-spacing("text");
}

@mixin hr-divider__text{
    -ms-high-contrast-adjust: none;
    background-color: inherit;
    padding: $divider-text-padding-default;
    position: relative;
    top: -(10/14) + rem;
    @include font-size-line-height($divider-text-size-default);
    margin: 0 auto;
}
