[class*="icon-"] {
    display: inline-block;

    &:before {
        @include font-size-line-height($icon-default);
        font-family: 'American Icons';
        font-style: normal;
        font-weight: normal;
        line-height: 1;
        speak: none;
        -webkit-font-smoothing: antialiased;
    }

}

.icon-small:before {
    @include font-size-line-height($icon-small);
}

.icon-default:before {
    @include font-size-line-height($icon-default);
}

