// Tablecloth variables
$tablecloth-bgcolor-primary: $darkBlue;
$tablecloth-bgcolor-primary-start: $tablecloth-bgcolor-primary;
$tablecloth-bgcolor-primary-stop: $detroitBlue;
$tablecloth-bgcolor-secondary: $superUltraLightGray;

$tablecloth-color-primary: $white;
$tablecloth-color-secondary: $darkGray;

$tablecloth-container-padding-primary: 50px 1rem;
$tablecloth-container-padding-secondary: 30px 1rem;
$tablecloth-container-padding-minimal: 1rem;

$tablecloth-container-maxwidth: 1040px;
$tablecloth-container-margin: 0 auto;

$tablecloth-headlines-margin-top: 0;


$tableclothStylesMap: (
    base: (
        primary: (
            'color': $tablecloth-color-primary,
            'background': $tablecloth-bgcolor-primary linear-gradient(
                to right,
                $tablecloth-bgcolor-primary-start,
                $tablecloth-bgcolor-primary-stop
            ),
            'tablecloth-container-padding': $tablecloth-container-padding-primary,
            'link-color': $tablecloth-color-primary,
            'link-focus-color': $tablecloth-color-primary,
            'headlines-color': $tablecloth-color-primary,
            'headlines-margin-top': $tablecloth-headlines-margin-top
        ),
        secondary: (
            'color': $tablecloth-color-secondary,
            'background': $tablecloth-bgcolor-secondary,
            'tablecloth-container-padding': $tablecloth-container-padding-primary,
            'headlines-color': $tablecloth-color-secondary,
            'headlines-margin-top': $tablecloth-headlines-margin-top
        )
    ),
    mobile: (
        primary: (
            'tablecloth-container-padding': $tablecloth-container-padding-secondary
        ),
        secondary: (
            'tablecloth-container-padding': $tablecloth-container-padding-secondary
        )
    )
);

// Tablecloth mixins

@mixin tablecloth-container-layout($padding){
    padding: $padding;
    max-width: $tablecloth-container-maxwidth;
    margin: $tablecloth-container-margin;
}

// adjust heading colors and margin spacing for headline elements
@mixin tablecloth-headlines($color, $margin-top){
    h1, h2, h3, h4, h5, h6 {
        color: $color;
        margin-top: $margin-top;
    }
}

// adjust link color and link:focus color
@mixin tablecloth-links($link-color, $link-focus-color){
    a {
        color: $link-color;

        &:focus {
            outline-color: $link-focus-color;
        }
    }
}
