.position-relative {
    position: relative !important;
}
.position-absolute {
    position: absolute !important;
}
.no-margin {
    margin: 0 !important;
}
.no-padding {
    padding: 0 !important;
}
.no-border {
    border: none !important;
}

.text{
    @each $type, $size in $font-sizes {
        &-#{$type}{
            @include font-text-size($type);
        }
    }
    @each $type, $color in $font-colors {
        &-#{$type}{
            @include font-text-color($type);
        }
    }
}
