$list-font-size: $global-font-size;
$list-line-height: $list-lineheight;
$list-item-padding-bottom: 0.33333333em;
$list-item-pro-con-padding-bottom: 0.66666667em;

li {
    padding-bottom: $list-item-padding-bottom;

    ul li:last-of-type, ol li:last-of-type {
        padding-bottom: 0;
    }
}

.list {
    $block: &;
    $element-item: "__item";
    $element-bullet: "__bullet";

    margin: 0 0 .5rem 0;

    &#{$element-item}{
        font-size: $list-font-size;
        font-size: 1em;
        line-height: $list-line-height;
        text-indent: -2em;
        padding-left: 2em;

        &--has-child{
            display: inline-block;
            padding-bottom: $list-item-padding-bottom;
        }
    }

    &#{$element-bullet}:before {
        font-family: American Icons;
        font-size: 24px;
        font-size: 1.6em;
        line-height: 0.1;
        padding-right: 6px;
        position: relative;
        overflow: hidden;
        top: 5px;
    }

    &--pros {
        list-style: none;
        #{$block}#{$element-item} {
            padding-bottom: $list-item-pro-con-padding-bottom;
        }
        #{$block}#{$element-bullet}:before {
            content: unicode("\10e049");
            color: $green;
        }
    }

    &--cons {
        list-style: none;
        #{$block}#{$element-item} {
            padding-bottom: $list-item-pro-con-padding-bottom;
        }
        #{$block}#{$element-bullet}:before{
            content: unicode("\10e04a");
            color: $darkRed;
        }
    }

    &--bullets-inside {
        list-style-position: inside;
    }
}
