/* Grid overrides */

.grid-container{
    padding-top: 20px;
    padding-bottom: 40px;
}

/* Fix for Safari 10 */
.grid-margin-y>.cell{
    min-height: initial;
}

@include breakpoint(small only) {
    .grid-container{
        background-color: $ultraLightGray;
        padding-left: 1em;
        padding-right: 1em;
    }
}