@mixin font-face($font-family, $file-path, $weight: normal, $style: normal, $file-formats: ()) {
    @font-face {
        font-family: $font-family;
        font-style: $style;
        font-weight: $weight;
        src: font-source-declaration($font-family, $file-path, $file-formats);
    }
}

@mixin font-size-line-height($font-size) {
    font-size: ($font-size / ($baseFontSize / ($baseFontSize * 0 + 1))) +rem;
    line-height: $font-size + $lineHeightIncrement + px;
}

@mixin focus-accessible{
    &:focus {
        outline: 2px solid $focusColor;
    }
}

@mixin double-shadow($borderColor){
    box-shadow: inset 0 0 0 1px $borderColor, 0 0 0 1px $borderColor;
}

@mixin shadow-background-color($color){
    box-shadow: 0 0 0 1px $color;
    background-color: $color;
}

@mixin shadow-image($xpos, $ypos){
    background-image: url($shadow-img);
    background-position: $xpos $ypos;
    background-repeat: no-repeat;
}

@mixin font-text-size($size){
    @if map-has-key($font-sizes, $size) {
        @include font-size-line-height(map-get($font-sizes, $size));
        @if $size == 'tiny' or $size == 'small' {
            font-family: $baseFontFamily;
        }
        @else if $size == 'large' or $size == 'xlarge' {
            font-family: $sansFontFamilyLight;
        }
    }
    @else{
        @warn "Unknown `#{$size}` in $font-sizes.";
    }
}

@mixin font-text-color($color){
    @if map-has-key($font-colors, $color) {
        color: map-get($font-colors, $color) !important;
    }
    @else{
        @warn "Unknown `#{$color}` in $font-colors.";
    }
}

@mixin box-shadow-elevation($elevation) {
    @if $elevation == 1 {
        box-shadow:
            0 4px 5px 0 rgba(0,0,0,0.14),
            0 1px 10px rgba(0,0,0, 0.12),
            0 2px 4px -1px rgba(0,0,0,0.20);
    }
}
