@import "divider-config";

/* Hr */
hr{
    width: 100%;
    margin: 20px 0;
    @include hr-divider-type('shadow');

    &.small{
        margin: 10px 0;
    }
    &.large{
        margin: 40px 0;
    }
}

.hr, .divider{
    &--section{
        @include hr-divider('section');
    }
    &--subsection{
        @include hr-divider('subsection');
    }
    &--shadow{
        @include hr-divider-type('shadow');
    }
    &--solid{
        @include hr-divider-type('solid');
    }
    &--dashed{
        @include hr-divider-type('dashed');
    }
    &--small{
        @include hr-divider-spacing('small');
    }
    &--medium{
        @include hr-divider-spacing('medium');
    }
    &--large{
        @include hr-divider-spacing('large');
    }
    &--no-margin{
        @include hr-divider-spacing('none');
    }
    &--with-text {
        @include hr-divider--with-text;
    }
    &__text {
        @include hr-divider__text;
        &--warning {
             color: getColor('warning');
        }
    }
}
