
table {
    border-collapse: collapse;

    thead {
        th {
            @include font-size-line-height(16);
        }
        tr {
            height: 50px;
        }
    }
    tbody, tfoot{
        tr{
            border-top: 1px solid $light-gray;
        }
    }
    td {
        vertical-align: text-top;
    }

    @include breakpoint(small only) {

        border: 1px solid $light-gray;

        thead tr {
            position: absolute;
            top: -9999em;
            left: -9999em;
        }

        tbody{
            tr {
                border-bottom: 1px solid $light-gray;
                border-top: none;
                display: block;
            }

            td {
                border: none;
                display: block;
                align-items: center;
                padding-left: 50%;
                position: relative;

                &:before {
                    content: attr(data-label);
                    display: inline-block;
                    font-weight: bold;
                    line-height: 1.5;
                    margin-left: -100%;
                    width: 100%;
                    position: relative;
                    z-index: 1;
                }

                &:after {
                    content: '';
                    position: absolute;
                    background: $superUltraLightGray;
                    width: 47%;
                    height: 100%;
                    left: 0;
                    bottom: 0;
                    z-index: 0;
                }
            }
        }
        tfoot{
            tr{
                border-top: none;
            }
        }
    }

}
