@import "tablecloth-config";

.tablecloth {
    @each $type, $params in map-get($tableclothStylesMap, 'base' ) {
        &--#{$type} {
            color: map-get($params, 'color');
            background: map-get($params, 'background');

            .tablecloth__container {

                @include tablecloth-container-layout( map-get($params, 'tablecloth-container-padding') );

                &--minPad {
                    padding: $tablecloth-container-padding-minimal;
                }

            }

            .tablecloth__content {
                @if map-has-key($params, 'link-color') {
                    @include tablecloth-links(
                        $link-color: map-get($params, 'link-color'),
                        $link-focus-color: map-get($params, 'link-focus-color')
                    );
                }

                @include tablecloth-headlines(
                    $margin-top: map-get($params, 'headlines-margin-top'),
                    $color: map-get($params, 'headlines-color')
                );

            }
        }
    }
}

@include breakpoint(small only) {
    .tablecloth {
        @each $type, $params in map-get($tableclothStylesMap, 'mobile' ) {
            &--#{$type} {
                .tablecloth__container {

                    @include tablecloth-container-layout( map-get($params, 'tablecloth-container-padding') );

                }
            }
        }
    }
}
