.button{
    $button-border-width: 2px;
    min-width: 80px;
    text-align: center;
    border-width: $button-border-width;
    background-clip: padding-box;
    margin: 0;

    &--inline {
        margin-top: -$button-border-width;
    }

    &:hover, &:active, &:focus{
        text-decoration: underline !important;
        border-color: initial;
    }
    &:focus, &.primary:focus{
        outline: none;
        @include shadow-background-color($brandBlue);
    }
    &:active{
        @include shadow-background-color($darkBlue);
    }

    &.disabled,
    &[disabled] {
        &:hover, &:focus {
            text-decoration: none !important;
            box-shadow: none;
        }
    }

    &.secondary, &.hollow, &.hollow.secondary{
        box-shadow: 0 0 0 1px $brandBlue inset;
        border: 2px solid transparent !important;
        color: $brandBlue;
        @include button-hollow;
        &:hover{
            color: $detroitBlue;
            box-shadow: 0 0 0 1px $detroitBlue inset;
        }
        &:focus{
            color: $brandBlue;
            @include double-shadow($brandBlue);
        }
        &:active{
            color: $darkBlue;
            @include double-shadow($darkBlue);
        }

        &.disabled,
        &[disabled] {
            &:hover, &:focus {
                box-shadow: 0 0 0 1px $brandBlue inset;
                color: $brandBlue;
            }
        }
    }
    @each $name, $color in $button-palette {
        @if $name != primary and $name != secondary {
            &:not(.hollow).#{$name} {
                &:focus, &:active{
                    @include shadow-background-color($color);
                }
                &.disabled,
                &[disabled] {
                    &:hover, &:focus {
                        box-shadow: none;
                    }
                }
            }
        }
        @if $name != secondary {
            &.hollow.#{$name}{
                box-shadow: 0 0 0 1px $color inset;
                &:focus, &:hover, &:active{
                    color: $color;
                }
                &:focus, &:active{
                    @include double-shadow($color);
                }
            }
        }
    }
    + .button:not(.expanded){
        margin-left: 1em;
    }
}
