@function unicode($str) {
    @return unquote("\"") + unquote(str-insert($str, "\\", 1)) + unquote("\"");
}

@function font-source-declaration($font-family, $file-path, $file-formats) {
    $src: ();

    $formats-map: (
        eot: "#{$file-path}.eot?#iefix" format("eot"),
        woff: "#{$file-path}.woff" format("woff"),
        ttf: "#{$file-path}.ttf" format("truetype"),
        otf: "#{$file-path}.otf" format("opentype"),
        svg: "#{$file-path}.svg##{$font-family}" format("svg")
    );

    @each $key, $values in $formats-map {
        @if index($file-formats, $key) {
            $file-path: nth($values, 1);
            $font-format: nth($values, 2);

            $src: append($src, url($file-path) $font-format, comma);
        }
    }

    @return $src;
}

@function asset($type, $file, $url: false) {
    @if $url {
        @return url("#{$base-asset-path}\/#{$type}\/#{$file}");
    } @else {
        @return "#{$base-asset-path}\/#{$type}\/#{$file}";
    }
}

@function getColor($keyword) {
    @if map-has-key($semantic-colors, $keyword) {
        @return map-get($semantic-colors, $keyword) !important;
    }
    @else {
        @warn "Unknown `#{$keyword}` in $semantic-colors.";
    }
}
