/* Primary colors */
$brandBlue: #0078D2;
$darkBlue: #00467F;
$detroitBlue: #0061AB;
$midnightBlue: #002849;
$darkRed: #C30019;
$green: #008712;
$orange: #D14904;

/* Grays */
$extremeUltraLightGray: #FAFBFB;
$superUltraLightGray: #F5F7F7;
$ultraLightGray: #EBEFF0;
$halfwayLightGray: #E7ECEF;
$lightGray: #D0DAE0;
$gray: #9DA6AB;
$mediumGray: #627A88;
$cadetGray: #536773;
$darkCadetGray: #465762;
$darkGray: #36495A;

/* Secondary */
$white: #FFFFFF;
$black: #131313;
$focusColor: #B0D1E8;
$darkOrange: #BE4204;
$luckyPurple: #931982;
$darkGreen: #007D10;
$lightOrange: #FAAF00;

$font-colors: (
    brand-blue: $brandBlue,
    dark-blue: $darkBlue,
    detroit-blue: $detroitBlue,
    dark-cadet-gray: $darkCadetGray,
    cadet-gray: $cadetGray,
    medium-gray: $mediumGray,
    dark-gray: $darkGray,
    orange: $orange,
    dark-red: $darkRed,
    green: $green,
    dark-green: $darkGreen,
    dark-orange: $darkOrange,
    light-orange: $lightOrange,
    lucky-purple: $luckyPurple
);

$semantic-colors: (
    info: $darkBlue,
    info-background: #e5f1fa,
    success: $green,
    success-background: #e5f3e7,
    warning: $orange,
    warning-background: #faece5,
    alert: $darkRed,
    alert-background: #f9e5e8
);

$h1-color: $brandBlue;
$h2-color: $darkBlue;
$h3-color: $mediumGray;
$h4-color: $mediumGray;
$h5-color: $darkGray;
$h6-color: $darkGray;

$h1-color-inverted: $white;

$baseFontFamily: Arial, sans-serif;
$sansFontFamilyLight: "AmericanSansLight", Helvetica, Arial, sans-serif;
$sansFontFamilyRegular: "AmericanSans", Helvetica, Arial, sans-serif;
$sansFontFamilyMedium: "AmericanSansMedium", Calibri, Helvetica, Arial, sans-serif;

$base-asset-path: "../fonts";

$legacy-font-types: woff ttf svg eot !default;
$latest-font-types: woff otf !default;

$font-family-list: (
    AmericanSans: (
        "americansans-regular": ("weight": "normal", "style": "normal", "types": $legacy-font-types),
        "americansans-regularitalic": ("weight": "normal", "style": "italic", "types": $legacy-font-types),
        "americansans-bold": ("weight": "bold", "style": "normal", "types": $legacy-font-types),
        "americansans-bold_italic": ("weight": "bold", "style": "italic", "types": $legacy-font-types)
    ),
    AmericanSansLight: (
        "americansans-light": ("weight": "normal", "style": "normal", "types": $legacy-font-types),
        "americansans-light_italic": ("weight": "normal", "style": "italic", "types": $legacy-font-types)
    ),
    AmericanSansMedium: (
        "americansans-medium-v2": ("weight": "normal", "style": "normal", "types": $latest-font-types),
        "americansans-medium_italic": ("weight": "normal", "style": "italic", "types": $legacy-font-types)
    ),
    "American Icons": (
        "american-icons-v5-0": ("weight": "normal", "style": "normal", "types": $latest-font-types)
    )
) !default;

$baseFontSize: 15px;
$baseLineHeight: 1.4;

/* Font sizes (in pixels) */
$tiny-size: 10!default;
$sm-size: 12!default;
$med-size: 21!default;
$lg-size: 27!default;
$xlg-size: 56!default;

$font-sizes: (
    tiny: $tiny-size,
    small: $sm-size,
    medium: $med-size,
    large: $lg-size,
    xlarge: $xlg-size
);

$h1-size: 38!default;
$h2-size: 30!default;
$h3-size: 24!default;
$h4-size: 18!default;
$h5-size: 16!default;
$h6-size: 14!default;

$sm-h1-size: 32!default;
$sm-h2-size: 27!default;
$sm-h3-size: 21!default;
$sm-h4-size: $h4-size;
$sm-h5-size: $h5-size;
$sm-h6-size: $h6-size;

$h1-lineHeight: 1.158;  //44px
$h2-lineHeight: 1.2;    //36px
$h3-lineHeight: 1.25;   //30px
$h4-lineHeight: 1.33;   //24px
$h5-lineHeight: 1.375;  //22px
$h6-lineHeight: 1.428;  //20px

$sm-h1-lineHeight: 1.187;   //38px
$sm-h2-lineHeight: 1.22;    //33px
$sm-h3-lineHeight: 1.285;   //27px
$sm-h4-lineHeight: $h4-lineHeight;
$sm-h5-lineHeight: $h5-lineHeight;
$sm-h6-lineHeight: $h5-lineHeight;

$lineHeightIncrement: 6!default;

$icon-small: 12 !default;
$icon-default: 14 !default;
$icon-medium: 18 !default;
$icon-large: 24 !default;
$icon-xlarge: 54 !default;
$icon-xxlarge: 70 !default;
$icon-xxxlarge: 86 !default;

$shadow-img: "data:image/png;base64,";
