/* Headings */
h1, .h1 {
    color: $h1-color;
}

h2, .h2 {
    color: $h2-color;
}

h3, .h3, h4, .h4{
    color: $h3-color;
}
h4, .h4{
    font-family: $sansFontFamilyRegular;
}
h5,.h5,h6,.h6{
    font-family: $baseFontFamily;
}

h1,h2,h3,h4,h5,h6,p,div,table,span,footer,header,nav,section {
    @include focus-accessible;
}

/* Links */
a{
    &:focus {
        outline: 2px solid $brandBlue;
        text-decoration: underline;
    }
    &.text-underline{
        &:hover, &:focus {
            text-decoration: inherit !important;
        }
    }
}

/* text decorators */
.call-to-action:after {
    content: " \00BB";
}
.return-to-previous:before {
    content: "\00AB ";
}

/* Superscript */
sup{
    top: .7em;
    vertical-align: top;
}

small {
    font-family: $baseFontFamily;
    color: $mediumGray;
    line-height: 1.5;
}

@include breakpoint(small only) {
    h1{
        margin-top: 1rem;
    }
    h1,h2,h3,h4,h5,h6,p,div,table,span,footer,header,nav,section,a {
        &:focus {
            outline: none;
        }
    }
}
