.card{
    h2, .h2{
        color: $detroitBlue;
    }
    .card-divider{
        border-bottom: 1px solid $ultraLightGray;
    }

    @include box-shadow-elevation(1);
}

@include breakpoint(medium) {
    .medium-up__no-shadow {
        box-shadow: none;
        overflow: visible;
    }
    .card {
        &.medium-up__no-shadow {
            .card-section{
                padding: 0;
            }
        }
    }
}

@include breakpoint(small only) {
    .card{
        h2, h3, h4{
            color: inherit;
        }
        h2, .h2, h3, .h3{
            font-family: $sansFontFamilyMedium;
        }
        h2, .h2{
            font-size: 1.3em;
            margin-bottom: 15px;
        }
        h3, .h3{
            font-size: 1.1em;
        }
        h4, .h4{
            font-size: 1em;
        }
    }
}
