@import "callout-config";

.callout{
    @include box-shadow-elevation(1);
    display: table;
    border: none;
    width: 100%;

    &:before {
        display: table-cell;
        width: 60px;
        min-height: 70px;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
        padding-bottom: 5px;
    }
    &:after {
        font-family: "American Icons";
        font-size: 38px;
        line-height: 55%;
        position: absolute;
        top: 38%;
        left: 2.1%;
    }
    #{$callout-headings}{
        margin-bottom: 0.572rem;
    }

    > :first-child {
        margin-top: 8px;
    }

    > :last-child {
        margin-bottom: 8px;
    }

    &-small{
        border-radius: 5px;
        color: $darkGray;
        font-family: $sansFontFamilyMedium;
        padding: 9px 25px 9px 53px;

        @each $type, $params in $callouts-small {
            &--#{$type} {
                border: 1px solid getColor($type + '-background');
                background-color: getColor($type + '-background');

                &:before {
                    font-family: 'American Icons';
                    font-size: 1.2em;
                    line-height: 21px;
                    color: getColor($type);
                    content: map-get($params, icon);
                    padding: 0 15px 0 0;
                    margin-left: -33px;
                    vertical-align: text-bottom;
                }
            }
        }

        a {
            color: $detroitBlue;
            font-family: $sansFontFamilyRegular;
        }
    }

    &-small ~ &-small {
        margin: 20px 0 0 0;
    }

    //.small is deprecated but remains for backward compatiblity for now
    &.small{
        padding: 10px;
        &:before{
            width: 40px;
            min-height: 42px;
            border-top-left-radius: 3px;
            border-bottom-left-radius: 3px;
        }
        &:after{
            left: 2%;
            top: 42%;
            font-size: 21px;
        }
        #{$callout-headings}{
            @include font-size-line-height(16);
            font-family: $sansFontFamilyRegular;
        }
        > *{
            margin-bottom: 4px;
        }
    }

    @each $type, $params in $callouts {
        &.#{$type} {
            background-color: $white;
            > *{
                margin-left: $callout-space;
            }
            @each $icon, $color in $params {
                #{$callout-headings}{
                    color: $color;
                    font-family: $sansFontFamilyLight;
                    @include font-size-line-height($h3-size);
                }
                &:before {
                    content: '';
                    background-color: $color;
                }
                &:after{
                    @extend .icon-#{$icon}:before;
                    color: $white;
                }
            }
        }
    }
}
@include breakpoint(xlarge down) {
    .callout{
        &:after, &.small::after{
            left: 2.5%;
        }
    }
}
@include breakpoint(large down) {
    .callout{
        &:after{
            left: 3.5%;
        }
        &.small::after{
            left: 2.7%;
        }
    }
}
@include breakpoint(medium down) {
    .callout{
        &:after{
            left: 4%;
        }
        &.small::after{
            left: 3.5%;
        }
    }
}
@include breakpoint(small only) {
    .callout{
        &:before {
            width: 50px;
            height: 55px;
        }
        &:after{
            left: 4%;
            font-size: 24px;
        }
        &.small::after{
            left: 3%;
        }
        > *:not(:last-child){
            margin-bottom: 5px;
        }
        @each $type, $params in $callouts {
            &.#{$type} {
                #{$callout-headings}{
                    color: $darkGray;
                    font-family: $sansFontFamilyMedium;
                    @include font-size-line-height(15);
                }
                &, &.small{
                    padding: 0 10px 0 0;
                }
                > :first-child{
                    margin-top: 10px;
                }
                > :last-child{
                    margin-bottom: 10px;
                }
            }
        }
    }
}
