.theme-dark{
    .button{
        &:not(.hollow){
            &:not(.secondary){
                background-color: $white;
                color: $brandBlue;
                @each $name, $color in $button-palette {
                    &.#{$name} {
                        color: $color;
                    }
                }
                &:focus, &:active{
                    box-shadow: 0 0 0 1px $white;
                }
            }
        }
        &.secondary{
            color: $white !important;
            &, &:hover{
                box-shadow: 0 0 0 1px $white inset;
            }
            &:focus, &:active{
                @include double-shadow($white);
            }
        }
    }
}