/* Callout variables */
$callouts: (
    primary: ( info: $darkBlue),
    secondary: ( info: $gray),
    success: ( info: $green),
    alert: ( error: $darkRed),
    warning: ( warning: $orange )
);

$callouts-small: (
    alert: ( icon: unicode("\10e03d")),
    warning: ( icon: unicode("\10e03e")),
    success: ( icon: unicode("\10e03c")),
    info: ( icon: unicode("\10e03c"))
);

$callout-space: 20px;
$callout-headings: 'h1,h2,h3,h4,h5,h6';


