$form-field-border-error: 1px solid $darkRed;
$form-field-border-focus: 1px solid $brandBlue;
$form-field-border-default: 1px solid $lightGray;
$radio-border-radius: 50%;
$fieldset-border-width: 0;

@mixin field--button-group($field-class: ("field__input", "field__select"), $button-class: "field__button") {
    @each $field in $field-class {
        .#{$field} {
            padding-right: 59px !important;
        }
    }

    .#{$button-class} {
        background-color: transparent;
        color: $mediumGray;
        height: 100%;
        margin: 0;
        max-height: 50px;
        min-width: initial;
        padding: 0;
        position: absolute;
        top: 0;
        right: 0;
        width: 50px;

        &:focus,
        &:hover {
            outline: 2px solid $brandBlue;
            border: 0;
            box-shadow: none;
        }
    }
}

@mixin aileron-fieldset($error-message-class: "error-message") {
    border: $fieldset-border-width;
    color: $mediumGray;
    padding: 0;
    padding-top: 5px;

    .#{$error-message-class} {
        display: none;
    }
}

@mixin fieldset--required {
    .fieldset__input .icon-required {
        display: none;
    }
}

@mixin fieldset--error($legend-class: "fieldset__legend", $input-class: "fieldset__input", $error-message-class: "error-message") {
    color: $darkRed;

    .#{$legend-class} {
        &::before {
            color: $darkRed;
            content: unicode("\10e03d");
            font-family: "American Icons";
            line-height: 1px;
            padding-right: 2px;
            position: relative;
            top: 1px;
        }
    }

    .#{$input-class} {
        label {
            display: block;

            &::before {
                content: '';
            }
        }

        input[type],
        select {
            border: $form-field-border-error;

            &:focus {
                border: $form-field-border-focus;
            }
        }

        .#{$error-message-class} {
            display: none;
        }
    }

    .#{$error-message-class} {
        display: inline-block;
        margin-top: 0;
    }
}

@mixin fieldset__group {
    align-content: stretch;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

@mixin fieldset__input {
    flex: 1;
    margin: 0;
    margin-right: 5px;
    margin-bottom: 5px;

    &:last-child {
        margin-right: 0;
    }
}

@mixin fieldset__legend {
    @include font-size-line-height(14);
    margin-left: 10px !important;
    padding: 0 !important;
}

.field {
    &--button-group {
        @include field--button-group;
    }
}

.form-field-container {
    font-family: $sansFontFamilyRegular;
    position: relative;
    margin-bottom: 20px;

    input[type="text"],
    input[type="email"],
    input[type="password"],
    input[type="month"],
    input[type="number"],
    input[type="search"],
    input[type="tel"],
    input[type="time"],
    textarea,
    select {
        margin: 0;
        border: $form-field-border-default;
        border-radius: 3px;
        padding: 22px 9px 0;
        min-height: 50px;
        box-shadow: none;
        @include font-size-line-height(14);
        &:hover {
            border: $input-prefix-border;
        }
        &:focus {
            border: $form-field-border-focus;
            box-shadow: none;
        }
        &:disabled {
            background: $ultraLightGray;
            border: 1px solid $ultraLightGray;
        }
    }
    select {
        color: $brandBlue;
        padding-right: 23px;
    }
    label {
        position: absolute;
        top: 5px;
        left: 9px;
        @include font-size-line-height(14);
        color: $form-label-color;
    }
    &.error {
        input,
        select {
            border: $form-field-border-error;
            &:focus {
                border: $form-field-border-focus;
            }
        }
        label {
            color: $darkRed;
            &:before {
                content: unicode("\10e03d");
                font-family: "American Icons";
                padding-right: 2px;
                color: $darkRed;
                line-height: 1px;
                position: relative;
                top: 1px;
            }
        }
        .error-message {
            display: inline-block;
        }
    }
    .error-message {
        display: none;
    }
    &--offset-top {
        margin-top: rem-calc(29);
    }
}

.error-message {
    color: $darkRed;
    margin: 5px 0 0 9px;
    font-family: $baseFontFamily;
    @include font-size-line-height(12);
}

.input-control-container {
    font-family: $sansFontFamilyRegular;
    margin-bottom: 20px;
    position: relative;
    input[type="checkbox"],
    input[type="radio"] {
        position: absolute;
        clip: rect(0, 0, 0, 0);
        overflow: hidden;
        margin: 0;
        ~ label {
            margin: 0;
            .input-control {
                display: inline-block;
                width: 22px;
                height: 22px;
                border: 1px solid $gray;
                position: relative;
                top: 6px;
                margin-right: 10px;
                background-color: $white;
            }
        }
        &:checked {
            ~ label {
                .input-control {
                    background: $brandBlue;
                    border: $form-field-border-focus;
                }
            }
        }
        &:focus {
            ~ label {
                .input-control {
                    border: $form-field-border-focus;

                    &::after {
                        border: 18px solid scale-color($brandBlue, $lightness: 90%);
                        border-radius: 50%;
                        bottom: 0;
                        content: '';
                        height: 100%;
                        left: -50%;
                        position: absolute;
                        right: 0;
                        top: -50%;
                        transform: translate(5%, 5%);
                        width: 20px;
                        z-index: -1;
                    }
                }
            }
        }
        &:disabled {
            ~ label {
                cursor: default;
                .input-control {
                    border: $form-field-border-default;
                }
            }
        }
    }
    input[type="radio"] {
        ~ label {
            .input-control {
                border-radius: $radio-border-radius;
                box-shadow: inset 0 0 0 3px $white, inset 0 0 0 4px $lightGray;
            }
        }
        &:checked {
            ~ label {
                .input-control {
                    box-shadow: inset 0 0 0 3px $white;
                    background: none;

                    &::before {
                        border: 7px solid $brandBlue;
                        border-radius: 50%;
                        content: '';
                        height: 1px;
                        left: 50%;
                        position: absolute;
                        top: 50%;
                        transform: translate(-50%, -50%);
                        width: 1px;
                    }
                }
            }
        }
    }
    input[type="checkbox"] {
        ~ label {
            .input-control {
                border-radius: 3px;
            }
        }
        &:checked {
            ~ label {
                .input-control {
                    &::before {
                        content: "";
                        width: 8px;
                        height: 14px;
                        position: absolute;
                        left: 6px;
                        border-top: none;
                        border-right: 2px solid $white;
                        border-bottom: 2px solid $white;
                        border-left: none;
                        @include rotate(45);
                    }
                }
            }
        }
    }
    &.error {
        input {
            ~ label {
                .input-control {
                    border: $form-field-border-error;
                }
            }
        }
        input {
            &:focus {
                ~ label {
                    .input-control {
                        border: $form-field-border-focus;
                    }
                }
            }
        }

        .error-message {
            display: inline-block;
        }
    }

    .error-message {
        display: none;
    }
}

.fieldset {
    @include aileron-fieldset;

    &--error {
        @include fieldset--error;
    }

    &--required {
        @include fieldset--required;
    }

    &__group {
        @include fieldset__group;
    }

    &__input {
        @include fieldset__input;
    }

    &__legend {
        @include fieldset__legend;
    }
}
